@import "./Mixins/Button.scss";
$mobile: 480px;
$tablet: 1024px;

.home {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  padding: 25px;
  margin: 0 auto;
  margin-top: 10%;

  width: 50%;

  background-image: linear-gradient(141deg, #dc143c, #8b0000);
  border-radius: 10px;

  @media screen and (max-width: $mobile) {
    width: 90%
  }

  p {
    color: #fffafa;
  }

  a {
    text-decoration: none;

    display: flex;
    flex-direction: column;
    align-items: center;

    width: 100%;
  }

  .button {
    display: flex;
    flex-direction: column;
    align-items: center;
    border-radius: 4px;
    background-color: #006400;
    border: none;
    color: #ffffff;
    text-align: center;
    font-size: 1.5rem;
    padding: 20px;
    width: 100%;
    transition: all 0.5s;
    cursor: pointer;
    margin: 5px;

    img {
      width: 2.5rem;
    }
  }

  .button span {
    cursor: pointer;
    display: flex;
    align-items: center;
    position: relative;
    transition: 0.5s;
  }

  .button span:after {
    content: "\00bb";
    position: absolute;
    opacity: 0;
    top: 0;
    right: -20px;
    transition: 0.5s;
  }

  .button:hover span {
    padding-right: 25px;
  }

  .button:hover span:after {
    opacity: 1;
    right: 0;
  }

  .casualModal {
    display: none;
    position: absolute;
    z-index: 1;
    padding-top: 20%;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto; /* Enable scroll if needed */
    background-color: rgb(0, 0, 0); /* Fallback color */
    background-color: rgba(0, 0, 0, 0.4); /* Black w/ opacity */

    .modal-content {
      position: relative;
      background-color: #fefefe;
      margin: auto;
      padding: 0;
      border: 1px solid #888;
      width: 30%;
      box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2),
        0 6px 20px 0 rgba(0, 0, 0, 0.19);
      -webkit-animation-name: animatezoom;
      -webkit-animation-duration: 0.6s;
      animation-name: animatezoom;
      animation-duration: 0.6s;

      @media screen and (max-width: $mobile) {
        width: 80%
      }
    }

    .close {
      color: white;
      float: right;
      font-weight: bold;

      &:hover,
      &:focus {
        color: #000;
        text-decoration: none;
        cursor: pointer;
      }
    }

    .modal-header {
      display: flex;
      justify-content: space-between;
      flex-direction: row-reverse;
      font-size: 1.4rem;
      padding: 2px 16px;
      background-color: #5cb85c;
      color: white;
    }

    .modal-body {
      display: flex;
      flex-direction: column;
      align-items: center;
      padding-bottom: 16px;

      input {
        @include btn(green);
        width: 50%;
        padding: 10px 20px;
      }
    }
  }

  .categoryDifModal {
    display: none;
    position: absolute;
    z-index: 1;
