    }

    .modal-body {
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      align-items: center;
      width: 100%;
      padding-bottom: 16px;

      @media screen and (max-width: $mobile) {
        width: 100%;
        display: flex;
        flex-direction: column;
      }

      div{
        display: flex;
        justify-content: space-between;
        width: 100%;

        @media screen and (max-width: $mobile) {
          width: 100%;
          display: flex;
          flex-direction: column;
        }
      }

      input {
        @include btn(#8e00ff);
        width: 100%;
        padding: 10px 20px;
      }
    }
  }
}

@keyframes animatezoom {
  from {
    transform: scale(0);
  }
  to {
    transform: scale(1);
  }
}

@-webkit-keyframes animatezoom {
  from {
    transform: scale(0);
  }
  to {
    transform: scale(1);
  }
}
